<template>
  <el-aside :width="isCollapse ? '65px' : '200px'">
    <el-tooltip
      class="item"
      effect="dark"
      content="合并/展开"
      placement="right"
    >
      <div class="toggle-button" @click="isCollapse = !isCollapse">
        <i class="el-icon-menu"></i>
      </div>
    </el-tooltip>

    <el-menu
      :default-active="select"
      class="el-menu-vertical-demo"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      :unique-opened="true"
      :collapse="isCollapse"
      :router="true"
    >
      <el-submenu :index="i + ''" v-for="(item, i) in menuList" :key="i">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span>{{ item.title }}</span>
        </template>
        <el-menu-item :index="item1.path" v-for="(item1, i) in item.children" :key="i">{{ item1.title }}</el-menu-item>
      </el-submenu>
    </el-menu>
  </el-aside>
</template>

<script>
export default {
  props: {
    menuList: {
      type: Array,
      default: () => [
        {
          title: '导航一',
          children: [
            { title: '选项1', path: 'index-one' },
            { title: '选项2', path: 'index-two' }
          ]
        },
        {
          title: '导航二',
          children: [
            { title: '选项1', path: 'index-three' },
            { title: '选项2', path: 'index-four' }
          ]
        },
        {
          title: '导航三',
          children: [
            { title: '选项1', path: 'index-five' },
            { title: '选项2', path: 'index-six' }
          ]
        },
        {
          title: '导航四',
          children: [
            { title: '选项1', path: 'index-seven' },
            { title: '选项2', path: 'index-eight' }
          ]
        },
        {
          title: '导航五',
          children: [
            { title: '选项1', path: 'index-nine' },
            { title: '选项2', path: 'index-ten' }
          ]
        }
      ]
    }
  },
  data () {
    return {
      isCollapse: false,
      select: null
    }
  },
  mounted () {
    const hashArr = window.location.href.split('/')
    this.select = hashArr[hashArr.length - 1]
  },
  watch: {
    $route: {
      handler: function (val, oldVal) {
        this.select = val.path.split('/')[val.path.split('/').length - 1]
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-aside {
  height: 100%;
  background-color: #d3dce6;
  color: #333;
  text-align: center;
  .toggle-button {
    height: 56px;
    line-height: 56px;
    cursor: pointer;
  }
  .el-menu {
    border: none;
  }
}
</style>
